import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import { AjusteInfoFiscal, CLASS_NAME } from '../../../../models/dtos/ger/ajusteInfoFiscal';

interface AjusteInfoFiscalEditViewProps extends EditViewProps<AjusteInfoFiscal> {}

const AjusteInfoFiscalEditView: FC<AjusteInfoFiscalEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<AjusteInfoFiscal>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Ajuste e Informações de Documentos Fiscais">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, ...onlyAlphaNum(0, 10) } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 400 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default AjusteInfoFiscalEditView;
